import React, { useState } from "react"
import { Link } from "react-router-dom"
import listingCSS from "../styles/listing.module.scss"
import movsho from "../images/svgs/movsho.svg"

const Listing = props => {
  const [blockDisplay, setBlockDisplay] = useState("grid")

  const [undoDisplay, setUndoDisplay] = useState("none")

  const [boxSize, setBoxSize] = useState("")

  const handleRemove = () => {
    setUndoDisplay("block")
    setBlockDisplay("none")
    setBoxSize("13rem")
  }

  const handleUndo = () => {
    setUndoDisplay("none")
    setBlockDisplay("grid")
    setBoxSize("")
  }

  return (
    <div style={{ height: boxSize }} className={listingCSS.listBlock}>
      <div style={{ display: blockDisplay }} className={listingCSS.content}>
        <img src={props.poster} alt={`Poster of: ${props.alt}`} />
        <div className={listingCSS.info}>
          <h3 className={listingCSS.name}>{props.name}</h3>
          <p className={listingCSS.plot}>{props.plot}</p>
          <p className={listingCSS.releaseDate}>
            <b>Release Date:</b> {props.date}
          </p>
          <p className={listingCSS.genre}>{props.genre}</p>
          <p style={{ color: "rgb(200, 158, 15)", fontSize: "2rem" }}>
            Ratings
          </p>
          <ul className={listingCSS.ratings}>
            <li>
              IMDB: <b>{props.imdbRating}</b>
            </li>
            <li>
              Rotten Tomatoes: <b>{props.tomatoRating}</b>
            </li>
            <li>
              MetaCritic: <b>{props.metaRating}</b>
            </li>

            <li>
              The Movie Database: <b>{props.tmdbRating}</b>
            </li>
          </ul>
          <p
            style={{
              fontSize: "1.6rem",
              padding: ".3rem",
              textAlign: "right"
            }}
          >
            <img
              style={{ width: "2rem" }}
              src={movsho}
              alt="Movsho All Yellow"
            />{" "}
            Movsho: <b>{props.movshoRating}</b>/100
          </p>
          <Link to={`${props.url}${props.name}`}>
            <button className={listingCSS.reviewBtn}>Video Reviews</button>
          </Link>
          <p className={listingCSS.stream}>Streaming: {props.streamOn}</p>
          <button
            className={listingCSS.removeBtn}
            onClick={() => handleRemove()}
          >
            Remove
          </button>
        </div>
      </div>

      <div style={{ display: undoDisplay }} className={listingCSS.undoBlock}>
        <p>{props.name}</p>

        <button onClick={() => handleUndo()} className={listingCSS.undoBtn}>
          Undo
        </button>
      </div>
    </div>
  )
}

export default Listing
